import axios from 'axios'
export default {
  name: 'projectFrame',
  data () {
    return {
      module: [],
      leader: '',
      activeNames: ['1']
    }
  },
  methods: {
    getFramework () {
      axios.get(this.url + '/project/frame/' + this.$route.params.pId)
        .then(this.getFrameworkSucc)
    },
    getFrameworkSucc (res) {
      if (res.status !== 200) {
        alert('该项目暂无架构！')
        this.$router.push('./frame')
      } else {
        res = res.data
        this.module = res.modules
        this.leader = res.leader
      }
    },
    handleChange (val) {
      console.log(val)
    }
  },
  mounted () {
    this.getFramework()
  }
}



